import { Fragment, useState } from 'react';
import { IconButton } from '@mui/material';
import * as Icons from '@mui/icons-material';
// import SaveIcon from '@mui/icons-material/Save';
// import CodeIcon from '@mui/icons-material/Code';
// import ListAltIcon from '@mui/icons-material/ListAlt';
// import RotateLeftIcon from '@mui/icons-material/RotateLeft';
// import ShuffleIcon from '@mui/icons-material/Shuffle';

import useGlobalState, { resetGlobalState, setCompleteGlobalState, initialState } from '../useGlobalState';
import { randomizeCharacter } from '../utils';
import { DIALOGS, SCREENS, REFRESH_TIMER } from '../utils/constants';

export default function Header() {
  const [, setActiveDialog] = useGlobalState('activeDialog');
  const [isSavingEnabled, setIsSavingEnabled] = useGlobalState('isSavingEnabled');
  const [isAuthenticated] = useGlobalState('isAuthenticated');
  const [color] = useGlobalState('color');
  const [, setScreen] = useGlobalState('screen');
  // Local state
  const [saveClickCount, setSaveClickCount] = useState(0);

  const handleEditClick = () => {
    if (!isSavingEnabled) {
      setSaveClickCount((v) => ++v);
      if (saveClickCount > 1) {
        setIsSavingEnabled(true);
      }
    }
  };

  const handleRandomizer = () => {
    setScreen(SCREENS.REFRESH);

    setTimeout(() => {
      setCompleteGlobalState(randomizeCharacter(initialState));
      setScreen(SCREENS.FORM);
    }, REFRESH_TIMER);
  };

  const handleOpenPrintDialog = () => {
    setActiveDialog(DIALOGS.PRINT);
  };

  const handleOpenCodeDialog = () => {
    setActiveDialog(DIALOGS.CODE);
  };

  const handleOpenImportDialog = () => {
    setActiveDialog(DIALOGS.IMPORT);
  };

  const handleOpenSaveDialog = () => {
    setActiveDialog(DIALOGS.SAVE);
  };

  const iconColor = color === 'white' || !color ? '#232c33' : '#ffffff';

  return (
    <header className={`header app-color--${color}`}>
      <IconButton
        className="header-button"
        aria-label="reset"
        title="Reset"
        onClick={resetGlobalState}
        size="large"
      >
        <Icons.RotateLeft style={{ color: iconColor }} />
      </IconButton>
      <h1 onClick={handleEditClick}>Roll Player: Hero Tome</h1>
      {(isSavingEnabled || isAuthenticated) && (
        <Fragment>
          <IconButton
            className="header-button"
            aria-label="import"
            title="Import"
            onClick={handleOpenImportDialog}
            size="large"
          >
            <Icons.ListAlt style={{ color: iconColor }} />
          </IconButton>
          <IconButton
            className="header-button"
            aria-label="save"
            title="Save"
            onClick={handleOpenSaveDialog}
            size="large"
          >
            <Icons.Save style={{ color: iconColor }} />
          </IconButton>
        </Fragment>
      )}
      <IconButton
        className="header-button"
        aria-label="randomize"
        title="Randomizer"
        onClick={handleRandomizer}
        size="large"
      >
        <Icons.Shuffle style={{ color: iconColor }} />
      </IconButton>
      <IconButton
        className="header-button"
        aria-label="print"
        title="Print"
        onClick={handleOpenPrintDialog}
        size="large"
      >
        <Icons.Print style={{ color: iconColor }} />
      </IconButton>
      <IconButton
        className="header-button"
        aria-label="code"
        title="Text"
        onClick={handleOpenCodeDialog}
        size="large"
      >
        <Icons.Code style={{ color: iconColor }} />
      </IconButton>
    </header>
  );
}
